import React from 'react';
import Chart from 'chart.js';

class CurrencyChart extends React.Component {

    constructor(props) {
        super(props);
        
        this.state = {
            options: this.props.options,
        };
    };

    componentDidMount() {
        let ctx = document.getElementById('chart-container').getContext('2d');
        this.setState({
            chart: new Chart(ctx, this.state.options),
        })
    }

    componentDidUpdate() {
        this.state.chart.render();
    }

    componentWillUnmount() {
        this.state.chart.destroy();
    }

    render() {
        let { to, from, labels } = this.props;
        return(
            <div id="modal-container">
                <h1>{from}-{to} exchange rate</h1>
                <canvas id="chart-container"></canvas>
                <div id="labels-container">
                    <div>{labels[0]}</div>
                    <div>{labels[labels.length - 1]}</div>
                </div>
            </div>
        )
    };
};

export default CurrencyChart;

